.Title {
    background-color: var(--main-red-color);
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}

.Title-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Title_header {
    margin: 0;
    padding: 0;
    font-size: 5rem;
}

.fname {
    color: var(--main-black-color);
}

.lname {
    color: var(--main-white-color);
}

.Little_header {
  font-size: 2rem;
  margin: 0;
  color: var(--main-black-color);
}

.headshot {
  height: 200px;
  width: 200px;
  border-radius: 50%;
  /* margin: 0 50% 0 50%; */
}

/* .curve {
    background: var(--main-red-color);
    color: white;
} */

.title-svg {
    display: block;
    background: var(--main-blue-color);
    /* position: absolute; */
    /* opacity: 0.5; */
}

/* @import url('https://fonts.googleapis.com/css?family=Source+Code+Pro'); */


@media (max-width: 660px) {
  .Title_header {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    font-size: 5rem;
  }

  .Title-content {
    position: absolute;
    left: 0px;
  }
}

@media (max-width: 320px) {
  .Title_header {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    font-size: 4rem;
  }

  .Title-content {
    position: absolute;
    left: 0px;
  }
}


@media (max-width: 1441px) {
  .Title_header {
    font-size: 5rem;
  }
}

@media (max-width: 2560px) {
  .Title_header {
    font-size: 8rem;
  }

  .headshot {
    height: 300px;
    width: 300px;
  }
}