}

.contact-me-header {
    z-index: 0;
    /* position: absolute; */
    font-size: 4.5rem;
    /* top: 0; */
}

.hills-svg-div {
    overflow: hidden;
    scroll-behavior: unset;
}

.contact-contents {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    flex-direction: column
}

.contact-list {
    list-style: none;
    display: flex;
    /* position: absolute; */
    padding: 0;
    margin: 0;
    font-size: 3em;

}
.list-item {
    padding: 5px;
    text-decoration: none;
    color: black;
}

.list-item:hover {
    color: var(--main-green-color);
}

